
footer {
    width: 100%;
    bottom: 0;
    display: block;
    text-align: center;
    position: relative;
    color: $c_wht_main;
    // background-color: $c_blue;
    background: linear-gradient(297deg, #0997dc 0%, #5c00b0 100%);

    #footer-info {
        width: 90%;
        max-width: 800px;
        display: inline-block;
        margin: 3.5rem auto 4rem auto;
        position: relative;
        padding-top: 1.5rem;

        .some-logo-row {
            margin-bottom: 30px;
            position: relative;

            a {
                width: 45px;
                height: 45px;
                display: inline-grid;
                border-radius: 50%;
                position: relative;
                margin-right: 15px;
                box-sizing: border-box;
                border: 2px solid $c_wht_main;
                background-color: transparent;
                transition: 0.15s;

                &::after {
                    content: '';
                    display: none;
                }

                img {
                    width: 25px;
                    display: block;
                    place-self: center;
                    position: relative;
                    transition: 0.1s;
                    user-select: none;
                    pointer-events: none;
                    filter: brightness(300%) grayscale(100%);
                }

                &:nth-child(1) { img {
                    width: 20px;
                    top: -1px;
                }}
                &:nth-child(2) { img {
                    width: 25px;
                }}
                &:nth-child(3) { img {
                    width: 20px;
                }}

                &:hover {
                    background-color: $c_wht_main;

                    img {
                        filter: brightness(100%) grayscale(0%);
                        // filter: brightness(100%) grayscale(0%) hue-rotate(205deg);
                    }
                }
            }
        }

        p {
            font-size: 0.95rem;
            text-align: center;
            letter-spacing: 0.02rem;

            &.about-react {
                font-size: 0.75rem;
                opacity: 0.7;
            }
        }
        a {
            color: $c_wht_main;

            &:hover {
                text-decoration: none;
            }
        }
    }

    @include sm {
        position: relative;
    }
}