section {
    &#section-front-page-intro {
        display: grid;
        grid-gap: 80px;
        margin: 0 auto;
        max-width: 1180px;
        margin-bottom: 80px;
        grid-template-columns: 630px 1fr;

        .front-page-intro-content {
            h2 {
                display: block;
                margin-bottom: 40px;
            }
            p {
                color: #444;
                font-size: 1.2rem;

                span {
                    color: $c_blue;
                    font-weight: 500;
                }
            }
            img.thumbs-up {
                width: 1.18rem;
                margin-left: 3px;
                display: inline-block;
            }
        }
        .front-page-intro-image {
            height: 100%;
            margin-top: 70px;
            margin-bottom: 30px;
            text-align: center;
            position: relative;
            max-width: 330px;
            justify-self: center;

            #screen-wrapper {
                z-index: 400;
                width: 100%;
                display: block;
                margin: 0 auto;
                // max-width: 330px;
                padding-bottom: 70%;
                overflow: hidden;
                position: relative;
                border-radius: 15px;
                background-color: #dbdbdb;

                &:after {
                    content: '';
                    top: 0;
                    left: 0;
                    width: 100%;
                    height: 87%;
                    display: block;
                    position: absolute;
                    background-color: #000;
                }

                #screen {
                    z-index: 500;
                    top: 7%;
                    left: 5%;
                    width: 90%;
                    height: 73%;
                    display: grid;
                    position: absolute;
                    background-color: $c_wht_main;

                    #screen-animation {
                        width: 85%;
                        overflow: visible;
                        place-self: center;
                    }

                    #screen-controls {

                        // HIDE ELEMENT

                        display: none;
                        width: 90%;
                        height: 20px;
                        bottom: 5px;
                        left: 50%;
                        // display: grid;
                        position: absolute;
                        column-gap: 6px;
                        transform: translateX(-50%);
                        grid-template-columns: 20px 1fr;
                        //background-color: rgba(0, 0, 0, 0.2);

                        button#screen-controls-play {
                            border: 0;
                            padding: 1px 3px 1px 5px;
                            border-radius: 50%;
                            transition: 0.15s;
                            background-color: transparent;

                            .play-icon {
                                width: 11px;
                                margin: 0;
                                filter: invert(100%);
                            }

                            &:hover {
                                background-color: rgba(0, 0, 0, 0.18);
                            }
                        }
                        label {
                            font-size: 0;
                            width: 0;
                            height: 0;
                            position: absolute;
                        }
                        // input#screen-controls-slider {
                            
                        // }
                    }
                }
            }

            img#screen-stand {
                z-index: 300;
                top: -1px;
                width: 35%;
                margin: 0 auto;
                display: inline-block;
                max-width: 330px;
                position: relative;
            }
        }

        @include xl {
            grid-gap: 60px;
            grid-template-columns: 440px 1fr;

            .front-page-intro-content {
                h2 {
                    margin-bottom: 40px;
                }
                p {
                    font-size: 1.1rem;
                }
            }
        }
        @include sm {
            grid-gap: 0;
            grid-template-columns: 100%;

            .front-page-intro-content {
                order: 2;

                h2 {
                    margin-bottom: 25px;
                }
                p {
                    font-size: 1.1rem;
                }
            }
            .front-page-intro-image {
                order: 1;
                width: 75vw;
                margin-top: 20px;

                img {
                    margin-top: 20px;
                    max-width: 300px;
                    margin-bottom: 30px;
                }
            }
        }
    }
}