// PROJECT SINGLE

.project-single {
    .project-single-header {
        display: block;
        position: relative;
        margin-bottom: 60px;

        p.project-single-category {
            margin: 0;
            color: $c_blue;
            font-size: 1.2rem;
            margin-right: 20px;
            display: inline-block;
            font-family: 'Nunito', Helvetica, sans-serif;
        }

        p.project-single-year {
            margin: 0;
            font-size: 0.865rem;
            letter-spacing: 0.05rem;
            color: $c_grey_light;
            display: inline-block;

            &:before {
                content: '';
                opacity: 0.4;
                height: 17px;
                width: 2px;
                top: 2px;
                margin-right: 17px;
                display: inline-block;
                position: relative;
                background-color: $c_grey_light;
            }
        }
        h2.project-single-title {
            margin-top: 7px;
            font-size: 4.0rem;

            &::after {
                content: none;
            }
        }
    }
    .project-single-intro {
        display: grid;
        grid-gap: 50px;
        margin-bottom: 50px;
        grid-template-columns: 250px 1fr;

        // h3 {
        //     font-size: 1.1rem;
        //     font-weight: 600;
        //     color: $c_blue;
        //     letter-spacing: 0.03rem;
        //     text-transform: uppercase;
        // }

        .project-single-intro-scope {
            ul {
                font-size: 1.1rem;
                margin-top: 0.5rem;
                padding-left: 20px;

                li {
                    &::marker {
                        color: $c_grey_light;
                    }
                }
            }
        }
        .project-single-intro-summary {
            width: 100%;
            max-width: 680px;

            p {
                font-size: 1.1rem;
                margin-top: 0.5rem;

                &.project-single-intro-disclaimer {
                    font-size: 0.875rem;
                    color: $c_grey_light;
                }
            }
        }
    }
    .project-single-content {
        img {
            width: auto;
            max-width: 100%;
            max-height: 95vh;
            margin: 0 auto;
            display: block;
            box-shadow: 0px 10px 24px rgba(0, 0, 0, 0.2);
        }
        img.no-shadow {
            box-shadow: initial;
        }
        button.video-initializer {
            display: grid;
            padding: 0;
            border: 0;
            margin: 0 auto;
            outline: none;
            position: relative;

            p {
                z-index: 500;
                display: block;
                place-self: center;
                color: $c_wht_main;
                position: absolute;
                padding: 13px 22px;
                border-radius: 30px;
                font-size: 1.1rem;
                transition: 0.2s;
                letter-spacing: 0.03rem;
                background-color: rgba(0, 0, 0, 0.4);
                font-family: 'Nunito', Helvetica, sans-serif;

                img.play-icon {
                    width: 12px;
                    margin-left: 7px;
                    display: inline-block;
                }
            }
            img {
                width: auto;
                height: auto;
                max-height: 80vh;
                margin: 0 auto;
                display: block;
                transition: 0.2s;
                position: relative;
                box-shadow: none;
            }

            &:hover {
                p {
                    transform: scale(1.05);
                    background-color: rgba(0, 0, 0, 0.6);
                }
                img {
                    opacity: 0.85;
                }
            }
        }
        video.video-element {
            width: 100%;
            max-height: 80vh;
            margin: 0 auto;
            display: block;
            box-shadow:
                0 2.8px 2.2px rgba(0, 0, 0, 0.034),
                0 6.7px 5.3px rgba(0, 0, 0, 0.048),
                0 12.5px 10px rgba(0, 0, 0, 0.06),
                0 22.3px 17.9px rgba(0, 0, 0, 0.072),
                0 41.8px 33.4px rgba(0, 0, 0, 0.086),
                0 100px 80px rgba(0, 0, 0, 0.12);
        } 



        .project-single-image-grid-2-column,
        .project-single-image-grid-3-column,
        .project-single-image-grid-4-column {
            p {
                margin: 0;
                display: block;
                font-size: 0.875rem;
                text-align: center;
                margin-bottom: 7px;
            }
            a {
                text-decoration: underline;

                &:after {
                    display: none;
                }
                &:Hover {
                    text-decoration: none;
                }
            }
        }

        .project-single-image-grid-2-column {
            display: grid;
            grid-row-gap: 50px;
            grid-column-gap: 4%;
            grid-template-columns: 55% 1fr;

            img {
                max-height: initial;
            }

            // .image-grid-desktop-block {
                
            // }
            .image-grid-mobile-block {
                img {
                    width: 47%;
                    margin: 0;
                    margin-right: -4px;
                    display: inline-block;
                    vertical-align: top;

                    &:nth-child(odd) {
                        margin-left: 6%;
                    }
                }
            }
        }
        .project-single-image-grid-3-column {
            display: grid;
            grid-row-gap: 30px;
            grid-column-gap: 2%;
            grid-template-columns: repeat(3, 1fr);

            img {
                max-height: initial;
            }
        }
        .project-single-image-grid-4-column {
            display: grid;
            grid-row-gap: 40px;
            grid-column-gap: 2%;
            grid-template-columns: repeat(4, 1fr);
        }
    }



    @include xl {
        .project-single-header {
            p.project-single-category {
                font-size: 1.1rem;
                margin-right: 20px;
            }
            h2.project-single-title {
                font-size: 3.2rem;
            }
        }
        .project-single-intro {
            grid-gap: 40px;
            margin-bottom: 50px;
            grid-template-columns: 200px 1fr;

            .project-single-intro-scope {
                ul {
                    font-size: 1.0rem;
                }
            }
            .project-single-intro-summary {
                width: 100%;
                max-width: 640px;

                p {
                    font-size: 1.0rem;
                }
            }
        }
        .project-single-content {
            .project-single-image-grid-3-column {
                grid-template-columns: repeat(2, 1fr);
            }
            .project-single-image-grid-4-column {
                grid-template-columns: repeat(2, 1fr);
            }
        }
    }
    @include sm {
        .project-single-header {
            margin-bottom: 30px;

            p.project-single-category {
                margin-right: 18px;
            }

            p.project-single-year {
                font-size: 0.8rem;

                &:before {
                    height: 15px;
                    margin-right: 16px;
                }
            }
        }
        .project-single-intro {
            grid-gap: 20px;
            margin-bottom: 50px;
            grid-template-columns: 100%;
        }
        .project-single-content {
            .project-single-image-grid-2-column {
                grid-column-gap: 0;
                grid-template-columns: 100%;

                img {
                    max-height: 250vh;
                }
            }
            .project-single-image-grid-3-column {
                grid-template-columns: 100%;
            }
        }
    }
    @include xs {
        .project-single-header {
            p.project-single-category {
                font-size: 0.95rem;
                margin-right: 20px;
            }
            h2.project-single-title {
                font-size: 2.4rem;
                margin-top: 4px;
            }
        }
        .project-single-content {
            .project-single-image-grid-2-column {
                .image-grid-mobile-block {
                    img {
                        width: 90%;
                        margin: 0 auto;
                        display: block;
                        max-height: none;
                        margin-bottom: 40px;

                        &:nth-child(odd) {
                            margin-left: auto;
                        }
                    }
                }
            }
            .project-single-image-grid-4-column {
                grid-template-columns: 1fr;
            }
        }
    }
}
