section {
    &#section-work {

        .projects {
            /* HIDDEN BY DEFAULT */
            display: block;
            position: relative;
            text-align: center;

            // &.active {
            //     display: block;
            // }

            h2.projects-subtitle {
                width: 100%;
                text-align: left;
                margin-bottom: 20px;

                &#projects-subtitle-development {
                    span {
                        color: $c_blue;
                    }
                }
                &#projects-subtitle-design {
                    span {
                        color: $c_red;
                    }
                }

                // &:after {
                //     content: '';
                //     width: 80px;
                //     height: 2px;
                //     display: block;
                //     margin-top: 8px;
                //     position: relative;
                //     background-color: $c_blue;
                // }
            }

            .projects-thumb-grid {
                display: grid;
                grid-template-columns: repeat(3, 1fr);
                grid-gap: 4%;
                row-gap: 6%;
                position: relative;
                margin-bottom: 240px;

                .project-wrapper {
                    position: relative;

                    &:nth-child(3n-2) {
                        top: 60px;
                        // transform: translateY(60px);
                    }
                    &:nth-child(3n-1) {
                        top: 30px;
                        // transform: translateY(30px);
                    }
    
                    a {
                        display: block;
                        text-align: center;
                        position: relative;
                        overflow: hidden;
                        // transition: box-shadow 0.25s;
                        // box-shadow: 0px 0px 0px rgba(0, 0, 0, 0.0);
    
                        img.project-image {
                            transition: 0.25s cubic-bezier(.25,.37,.55,1);
                            // filter: grayscale(0.7);
                            transform-origin: top left;
                        }
    
                        .thumb-overlay {
                            opacity: 1;
                            top: 0;
                            left: 0;
                            width: 100%;
                            height: 100%;
                            transition: .25s;
                            position: absolute;
                            // background-color: rgba(0, 0, 0, 0.4);
    
                            .overlay-info {
                                // transform: translateY(100%);
                                bottom: 0;
                                right: 0;
                                width: 0%;
                                height: 95px;
                                position: absolute;
                                border-radius: 7px 0 7px 7px;
                                box-shadow: -8px -9px 15px rgba(0, 0, 0, 0.18);
                                transition: .25s cubic-bezier(.25,.37,.55,1);
                                background-color: $c_red;
    
                                p {
                                    width: 90%;
                                    opacity: 0;
                                    line-height: 1.1;
                                    font-size: 1.4rem;
                                    font-weight: 600;
                                    color: $c_wht_main;
                                    letter-spacing: 0.02rem;
                                    // margin-left: -10%;
                                    // text-transform: uppercase;
                                    transform: translateX(20%);
                                    font-family: 'Nunito', Helvetica, sans-serif;
                                    transition: transform 0.45s cubic-bezier(.25,.37,.55,1), opacity 0.35s 0.1s;
    
                                    span {
                                        opacity: 0.7;
                                        font-size: 0.95rem;
                                        font-weight: 100;
                                        text-transform: none;
                                        // color: $c_red;
                                    }
                                }

                                img.overlay-info-arw {
                                    opacity: 0;
                                    top: 40px;
                                    right: 25px;
                                    width: 20px;
                                    display: block;
                                    position: absolute;
                                    transform: rotate(180deg) translateX(-20px);
                                    transition: 0.15s;
                                    transition-delay: opacity 0s;

                                    &:hover {
                                        right: 20px;
                                    }
                                }

                                &:after {
                                    content: '';
                                    top: -19px;
                                    right: 0;
                                    width: 5.7%;
                                    height: 20px;
                                    position: absolute;
                                    background-color: $c_red;
                                    // clip-path: polygon(0 20%, 0% 100%, 100% 100%);
                                    clip-path: polygon(0 0, 100% 85%, 100% 100%, 0% 100%);
                                }
                            }
                        }
                    }
    

                    // Add hover effect only for desktop views
                    @media (min-width: 769px) {
                        &:hover {
                            a {
                                //box-shadow: 0px 4px 14px rgba(0, 0, 0, 0.25);
        
                                img.project-image {
                                    transform: scale(0.95);
                                }
                                .thumb-overlay {
                                    opacity: 1;
            
                                    .overlay-info {
                                        width: 90%;
                                        // transform: translateY(0px);
            
                                        p {
                                            opacity: 1;
                                            transform: translateX(0%);
                                        }
                                        img.overlay-info-arw {
                                            opacity: 1;
                                            transition: 0.35s;
                                            transition-delay: opacity 0.15s;
                                            transform: rotate(180deg) translateX(0px);
                                        }
                                    }
                                }
                            }
                        }
                    }
                }
            }
        }



        @include xl {
            .projects {
                .projects-thumb-grid {
                    grid-template-columns: repeat(2, 1fr);
                    grid-gap: 5%;
                    row-gap: 4%;
                    margin-bottom: 30%;
        
                    .project-wrapper {
                        &:nth-child(3n-2) {
                            top: 0;
                            // transform: none;
                        }
                        &:nth-child(3n-1) {
                            top: 0;
                            // transform: none;
                        }
        
                        &:nth-child(odd) {
                            top: 30px;
                            // transform: translateY(30px);
                        }
                    }
                }
            }
        }
        @include sm {
            .projects {
                .projects-thumb-grid {
                    margin-bottom: 35%;
        
                    .project-wrapper {
                        a {        
                            img.project-image {
                                transform: scale(0.95);
                            }
        
                            .thumb-overlay {
        
                                .overlay-info {
                                    width: 95%;
                                    height: auto;
                                    min-height: 78px;
                                    border-radius: 6px 0 6px 6px;
        
                                    p {
                                        width: 80%;
                                        opacity: 1;
                                        line-height: 1.05;
                                        font-size: 1.2rem;
                                        margin-left: 8%;
                                        text-align: left;
                                        transform: translateX(0%);
        
                                        span {
                                            font-size: 0.85rem;
                                        }
                                    }
        
                                    img.overlay-info-arw {
                                        opacity: 1;
                                        top: initial;
                                        bottom: 33px;
                                        right: 20px;
                                        width: 17px;
                                        transform: rotate(180deg) translateX(0px);
                                    }
        
                                    &:after {
                                        top: -13px;
                                        width: 5.2%;
                                        height: 14px;
                                    }
                                }
                            }
                        }
                    }
                }
            }
        }
        @include xs {
            .projects {
                .projects-thumb-grid {
                    grid-template-columns: repeat(1, 1fr);
                    grid-gap: 0;
                    row-gap: 30px;
                    margin-bottom: 80px;
        
                    .project-wrapper {
                        &:nth-child(3n-2) {
                            top: 0;
                            // transform: none;
                        }
                        &:nth-child(3n-1) {
                            top: 0;
                            // transform: none;
                        }
        
                        &:nth-child(odd) {
                            top: 0;
                            // transform: none;
                        }
                    }
                }
            }
        }
    }
}