
section {
    width: 100%;
    display: block;
    position: relative;
    box-sizing: border-box;
    // padding: 30px 50px .5rem 50px;

    a {
        position: relative;
        color: $c_red;
        transition: 0.1s;
        text-decoration: none;

        &::after {
            content: '';
            bottom: -1px;
            left: 50%;
            width: 90%;
            height: 1px;
            margin: 0 auto;
            position: absolute;
            transition: 0.15s;
            transform: translateX(-50%);
            background-color: $c_red;
        }

        &:hover {
            color: $c_red;
            
            &::after {
                width: 0%;
            }
        }
    }

    a.back-to-parent {
        display: inline-block;
        font-size: 0.875rem;
        color: $c_red;
        letter-spacing: 0.03rem;
        border: solid 2px $c_red;
        text-decoration: none;
        padding: 9px 20px 9px 16px;
        text-transform: uppercase;
        border-radius: 25px;
        margin-bottom: 3rem;
        transition: 0.15s;

        img {
            width: 0.80rem;
            margin-right: 7px;
            display: inline-block;
            transition: 0.15s;
        }

        &:hover {
            color: $c_wht_main;
            background-color: $c_red;

            img {
                filter: grayscale(100%) brightness(200%);
                transform: translateX(-5px);
            }
        }
        &:after {
            display: none;
        }
    }

    a.next-page-link {
        color: $c_wht_main;
        font-size: 1.05rem;
        font-weight: 500;
        position: relative;
        display: inline-block;
        text-decoration: none;
        letter-spacing: 0.02rem;
        background-color: $c_red;
        border: 2px solid $c_red;
        border-radius: 30px;
        padding: 10px 23px;
        margin-top: 5px;
        transition: 0.20s;
        font-family: 'Nunito', Helvetica, sans-serif;

        &:after {
            content: '';
            width: 18px;
            height: 13px;
            top: 0px;
            left: initial;
            margin-left: 9px;
            position: relative;
            display: inline-block;
            background-size: cover;
            background-repeat: no-repeat;
            background-position: center;
            background-color: transparent;
            transition: 0.20s;
            transform: initial;
            filter: grayscale(100%) brightness(200%);
            background-image: url(/assets/icons/arw-right.svg);
        }

        &:hover {
            color: $c_red;
            border: 2px solid $c_red;
            background-color: $c_wht_main;

            &:after {
                transform: translateX(5px);
                filter: grayscale(0%) brightness(100%);
            }
        }

        &.btn-shine {
            overflow: hidden;

            &:before {
                content: "";
                position: absolute;
                top: 0;
                left: -150%;
                width: 250px;
                height: 80px;
                /*opacity: 1;*/
                pointer-events: none;
                user-select: none;
                transform: rotate(-50deg);
                background: rgba(255, 255, 255, 0.25);
                background: linear-gradient(
                    to top, 
                    rgba(255, 255, 255, 0.0) 0%,
                    rgba(255, 255, 255, 0.4) 30%,
                    rgba(255, 255, 255, 0.0) 100%
                );
                animation: shine 4.2s 1.5s ease-in-out 20;
            }
            @keyframes shine {
                0% { left: -150%; }
               20% { left: 100%; }
              100% { left: 100%; }
            }
        }
    }

    // @media (max-width: 768px) {
    //     padding-left: 30px;
    //     padding-right: 30px;
    // }
    // @media (max-width: 380px) {
    //     padding-left: 25px;
    //     padding-right: 25px;
    // }

}