
$c_wht_main: #fff;
$c_blk_main: #222931;
$c_grey_main: #65696e;
$c_grey_light: #91959b;
$c_bluedark: #0e1213;
$c_red: #ff5c5f;
$c_blue: #0997dc;
$c_purple: #5c00b0;



/*--- MEDIA QUERIES ---*/
// Usage example for this: @include xs {}

$screen-xs-max: 576px;
$screen-sm-max: 768px;
$screen-lg-max: 991px;
$screen-xl-max: 1200px;


// Small devices
@mixin xs {
    @media (max-width: #{$screen-xs-max}) {
        @content;
    }
}
 
// Medium devices
@mixin sm {
    @media (max-width: #{$screen-sm-max}) {
        @content;
    }
}
 
// Large devices
@mixin lg {
    @media (max-width: #{$screen-lg-max}) {
        @content;
    }
}
 
// Extra large devices
@mixin xl {
    @media (max-width: #{$screen-xl-max}) {
        @content;
    }
}
