
header#app-header {
    height: 23vw;
    max-height: 400px;
    display: block;
    overflow: hidden;
    position: relative;
    margin-bottom: 50px;
    padding-top: 70px;
    box-sizing: border-box;
    border-bottom: 2px solid $c_wht_main;
    background: linear-gradient(145deg, $c_blue 0%, $c_purple 100%);
    mask-image: url(../images/hero-bg_wavemask.svg);
    mask-repeat: repeat-x;
    mask-position: 0% 102%;
    mask-size: cover;

    @media (min-width: 2160px) {
        height: 16vw;
        mask-size: initial;
        margin-bottom: 5vw;
    }


    // &::after {
    //     content: '';
    //     bottom: -1px;
    //     left: 0;
    //     width: 100%;
    //     height: 12.5vw;
    //     position: absolute;
    //     background-size: cover;
    //     background-repeat: no-repeat;
    //     background-position: center;
    //     background-image: url(../images/hero-bg_wave.svg);
    // }

    #header-container {
        display: grid;
        grid-template-columns: 350px 1fr;
        grid-gap: 20px;
    }

    #site-title {
        text-align: left;
        text-decoration: none;

        a {
            width: auto;
            display: inline-block;
            text-decoration: none;

            h1 {
                width: auto;
                margin: 0;
                font-weight: 600;
                font-size: 1.8rem;
                line-height: 1.2;
                color: $c_wht_main;
                text-align: left;
                position: relative;
                display: inline-block;
                transition: color 0.2s;

                // screen-reader-text
                span.screen-reader-text {
                    // top: -10000em;
                    width: 1px;
                    height: 1px;
                    margin: -1px;
                    padding: 0;
                    border: 0;
                    overflow: hidden;
                    clip: rect(0,0,0,0);
                    position: absolute;
                }

                // Logo images
                img {
                    width: auto;
                    height: 30px;
                    position: relative;
                    display: inline-block;
                    transition: filter 0.2s;
                    filter: grayscale(100%) brightness(300%);

                    &#logo-aleksi {
                        margin-right: 10px;
                    }
                }
    
                // Portfolio text
                span {
                    opacity: 0.55;
                    color: $c_wht_main;
                    // font-weight: 100;
                    font-size: 0.9rem;
                    letter-spacing: 0.02rem;
                    top: 0.0rem;
                    display: block;
                    position: relative;
                }
            }

            &:hover {
                h1 {
                    color: $c_red;

                    img {
                        filter: grayscale(0%) brightness(100%);
                    }
                }
            }
        }
    }

    nav {
        margin-top: -40px;
        align-self: center;

        ul {
            text-align: right;
            margin: 0 auto;
            padding: 0;

            li {
                min-width: 80px;
                margin: 0 20px;
                display: inline-block;

                a {
                    position: relative;
                    color: $c_wht_main;
                    font-size: 1.3rem;
                    font-weight: 600;
                    text-decoration: none;
                    letter-spacing: 0.05rem;
                    font-family: 'Nunito', Helvetica, sans-serif;
                    transition: color .1s;

                    &:hover {
                        color: $c_red;
                    }

                    &.active {
                        color: $c_red;

                        &::after {
                            width: 80px;
                        }
                    }

                    &::after {
                        content: '';
                        bottom: -7px;
                        left: 50%;
                        width: 0%;
                        height: 1px;
                        margin: 0 auto;
                        position: absolute;
                        transition: 0.15s;
                        transform: translateX(-50%);
                        background-color: $c_red;

                    }
                }
            }
        }
    }


    @include xl {
        height: 30vw;
        max-height: 250px;
        padding-top: 50px;

        #header-container {
            grid-template-columns: 280px 1fr;
        }

        #site-title {
            a {
                h1 {
                    font-size: 1.6rem;
                }
            }
        }
    
        nav {
            ul {
                margin: 0 auto;
    
                li {
                    margin: 0 10px;
    
                    a {
                        font-size: 1.1rem;
    
                        &::after {
                            bottom: -7px;
                        }
                    }
                }
            }
        }
    }



    @include sm {
        height: 220px;
        padding-top: 40px;
        mask-size: cover;
        mask-position: 0% 110%;

        #header-container {
            grid-template-columns: 140px 1fr;
        }

        #site-title {
            a {
                h1 {
                    font-size: 1.6rem;

                    img {
                        height: 24px;
                        display: block;
    
                        &#logo-aleksi {
                            margin-right: 0;
                            margin-bottom: 4px;
                        }
                    }
    
                    span {
                        top: 0.35rem;
                        font-size: 0.8rem;
                    }
                }
            }
        }
    }

    @include xs {
        nav {
            margin-top: 0px;

            ul {
                margin: 0 auto;
    
                li {
                    min-width: 100%;
                    margin: 0;
                    margin-bottom: 20px;
    
                    a {
                        &.active {    
                            &::after {
                                left: initial;
                                right: -15%;
                                width: 70px;
                                bottom: -4px;
                                transform: none;
                            }
                        }
                    }
                }
            }
        }
    }

    @media (max-width: 380px) {
        height: 200px;
        padding: 30px 0 100px 0;

        #site-title {
            a {
                h1 {
                    font-size: 1.5rem;
                }
            }
        }

        nav {
            margin-top: -17px;

            ul {    
                li {
                    margin: 6px auto;
    
                    a {
                        font-size: 1.0rem;
                    }
                }
            }
        }
    }

}