
html {
    margin: 0;
    padding: 0;
    font-size: 16px;
    color: $c_blk_main;
    background-color: $c_wht_main;
    text-rendering: optimizeLegibility;
    font-family: 'Poppins', Helvetica, sans-serif;
}
body {
    margin: 0;
}
h1 {
    font-size: 1.2rem;
    font-weight: 200;
    text-align: center;
    color: $c_blk_main;
    font-family: 'Nunito', Helvetica, sans-serif;
}
h2 {
    margin: 0;
    font-size: 3.2rem;
    font-weight: 600;
    color: $c_blk_main;
    line-height: 1.2;
    font-family: 'Nunito', Helvetica, sans-serif;

    &:after {
        content: '';
        width: 80px;
        height: 2px;
        display: block;
        margin-top: 8px;
        position: relative;
        background-color: $c_blue;
    }
}
h3 {
    margin: 0;
    color: $c_blue;
    font-size: 1.1rem;
    font-weight: 600;
    letter-spacing: 0.03rem;
    margin-bottom: 10px;
    text-transform: uppercase;
    font-family: 'Nunito', Helvetica, sans-serif;
}
p {
    font-weight: 400;
}
img {
    width: 100%;
    display: block;
}




.container {
    width: 85%;
    max-width: 1400px;
    margin: 0 auto;
    position: relative;
    box-sizing: border-box;
    // box-shadow: 0px 0px 12px 0px rgba(0,0,0,0.35);

    &#main-content {
        display: table;
        min-height: 65vh;
        padding-bottom: 100px;
        background-color: $c_wht_main;
    }

    @include sm {
        &#main-content {
            padding-bottom: 20px;
        }
    }
    @include xs {
        width: 100%;
        padding: 0 30px;
    }
}







@include xl {
    h2 {
        font-size: 2.7rem;
    }
    h3 {
        font-size: 1.0rem;
    }
}


@media (max-width: 768px) {
    h2 {
        font-size: 2.15rem;
    }
    h3 {
        font-size: 0.95em;
    }
}

