section {
    &#section-about {
        display: grid;
        grid-gap: 80px;
        margin: 0 auto;
        max-width: 1180px;
        margin-bottom: 80px;
        grid-template-columns: 630px 1fr;

        .section-about-info {
            width: 100%;
            display: block;
            position: relative;

            h3 {
                margin-top: 20px;
            }
            p {
                b {
                    color: $c_blue;
                }
            }
            a {
                position: relative;
                color: $c_red;
                transition: 0.1s;
                text-decoration: none;
    
                &::after {
                    content: '';
                    bottom: -1px;
                    left: 50%;
                    width: 90%;
                    height: 1px;
                    margin: 0 auto;
                    position: absolute;
                    transition: 0.15s;
                    transform: translateX(-50%);
                    background-color: $c_red;
                }
    
                &:hover {
                    color: $c_red;
                    
                    &::after {
                        width: 0%;
                    }
                }
            }

            .info-list-wrapper {
                display: grid;
                grid-row-gap: 15px;
                grid-column-gap: 5%;
                grid-template-columns: 48.5% 48.5%;

                ul {
                    margin-top: 0.0rem;
                    padding-left: 20px;
    
                    li {
                        &::marker {
                            color: $c_grey_light;
                        }
                    }
                }
            }

    
            .some-logo-row {
                margin-top: 30px;
                position: relative;
    
                a {
                    width: 55px;
                    height: 55px;
                    display: inline-grid;
                    border-radius: 50%;
                    position: relative;
                    margin-right: 15px;
                    box-sizing: border-box;
                    border: 2px solid $c_red;
                    background-color: $c_wht_main;
                    transition: 0.15s;

                    &::after {
                        content: '';
                        display: none;
                    }
    
                    img {
                        width: 30px;
                        display: block;
                        user-select: none;
                        pointer-events: none;
                        place-self: center;
                        position: relative;
                        transition: 0.15s;
                    }
    
                    &:nth-child(1) { img {
                        width: 25px;
                        top: -1px;
                    }}
                    &:nth-child(2) { img {
                        width: 30px;
                    }}
                    &:nth-child(3) { img {
                        width: 26px;
                    }}
    
                    &:hover {
                        background-color: $c_red;
    
                        img {
                            filter: brightness(300%) grayscale(100%);
                        }
                    }
                }
            }
    
            // p.about-react {
            //     opacity: 0.65;
            //     margin-top: 80px;
            //     font-size: 0.875rem;
            // }
        }

        .section-about-image {
            text-align: center;

            img {
                width: 100%;
                display: inline-block;
                margin-top: 50px;
                margin-bottom: 20px;
                max-width: 380px;
                position: relative;
            }
            // p.illustration-credit {
            //     opacity: 0.7;
            //     transition: opacity 0.15s;
            //     margin-bottom: 40px;
            //     color: $c_grey_light;
            //     font-size: 0.65rem;
            //     letter-spacing: 0.02rem;

            //     a {
            //         color: $c_grey_light;
            //         text-decoration: underline;

            //         &:after {
            //             display: none;
            //         }
            //     }
            // }
        }

        @include xl {
            grid-gap: 60px;
            grid-template-columns: 440px 1fr;
        }
        @include sm {
            grid-gap: 0;
            grid-template-columns: 100%;

            .section-about-info {
                order: 2;
            }
            .section-about-image {
                order: 1;

                img {
                    margin-top: 20px;
                }
            }
        }
        @include xs {
            .section-about-info {
                .info-list-wrapper {
                    grid-column-gap: 0%;
                    grid-template-columns: 100%;
                }
            }
        }
    }
}